.chooseFile {
  width: 100%;
}

.title {
  color: var(--secondary);
  font-size: 2rem;
  font-weight: bold;
  border-bottom: 1px solid var(--primary);
}

.chooseFile ul {
  display: flex;
  margin-top: -1rem;
  margin-left: -1rem;
}

.chooseFile li {
  list-style: none;
  margin-top: 0;
  margin-right: 1.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
}

.chooseFile li.active {
  color: var(--primary);
  border-bottom: 1px solid var(--primary);
}

.content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin-left: 1.5rem; */
}

.content p {
  font-weight: 600;
  font-size: 1.2rem;
  margin-right: 1.5rem;
}

.content input {
  padding: 0 1rem;
  height: 2rem;
  width: 20rem;
  outline: none;
  border-radius: 5px;
  border: 2px solid var(--primary);
  font-size: 1rem;
  letter-spacing: 1px;
}
