.card {
  width: 50%;
  height: 24rem;
  margin: 0 auto;
  background-color: #f5f5f5;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom: 3px solid var(--secondary);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.container {
  width: 90%;
  margin: 0 auto;
}

.progressBar {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.buttons {
  position: absolute;
  right: 5%;
  bottom: 25%;
}

.nextButton {
  padding: 0.75rem 1.5rem;
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  border: none;
  background-color: var(--primary);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.35s ease;
  margin-top: 2rem;
  margin-left: 0.75rem;
}

.nextButton:hover {
  transform: translateY(-1px);
  background-color: var(--primary-dark);
}

.nextButton:focus,
.nextButton:active {
  outline: none;
}

.nextButton.disabled {
  background-color: #bbb;
  cursor: not-allowed;
  /* pointer-events: none; */
}

.nextButton.hidden {
  display: none;
}
