@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;500;600;700;800&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f5f5f5;
  font-family: "Baloo Da 2", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primary: #4393d7;
  --primary-dark: #3b83c2;
  --secondary: #333;
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: linear-gradient(to right, #4393d7, #3b83c2);
  background-image: linear-gradient(to right, var(--primary), var(--primary-dark));
}

.title {
  font-size: 3rem;
  font-weight: bold;
  color: #f5f5f5;
  margin-bottom: 1rem;
}

.title span {
  color: #333;
  color: var(--secondary);
  text-transform: uppercase;
}

.Card_card__1EEvq {
  width: 50%;
  height: 24rem;
  margin: 0 auto;
  background-color: #f5f5f5;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom: 3px solid var(--secondary);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.Card_container__xUtDs {
  width: 90%;
  margin: 0 auto;
}

.Card_progressBar__15KfW {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.Card_buttons__3fjYy {
  position: absolute;
  right: 5%;
  bottom: 25%;
}

.Card_nextButton__1lNW5 {
  padding: 0.75rem 1.5rem;
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  border: none;
  background-color: var(--primary);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.35s ease;
  margin-top: 2rem;
  margin-left: 0.75rem;
}

.Card_nextButton__1lNW5:hover {
  transform: translateY(-1px);
  background-color: var(--primary-dark);
}

.Card_nextButton__1lNW5:focus,
.Card_nextButton__1lNW5:active {
  outline: none;
}

.Card_nextButton__1lNW5.Card_disabled__3LbEG {
  background-color: #bbb;
  cursor: not-allowed;
  /* pointer-events: none; */
}

.Card_nextButton__1lNW5.Card_hidden__12wTX {
  display: none;
}

