.downloadFile {
  width: 100%;
  height: 100%;
  position: relative;
}

.title {
  color: var(--secondary);
  font-size: 2rem;
  font-weight: bold;
  border-bottom: 1px solid var(--primary);
}

.button {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: 9rem;
  transform: translate(-50%, -50%);
  padding: 0.5rem 1rem;
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  border: none;
  background-color: var(--primary);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.35s ease;
}

.button:hover {
  background-color: var(--primary-dark);
}

.button:focus,
.button:active {
  outline: none;
}
