.button {
  padding: 0.5rem 1rem;
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  border: none;
  background-color: var(--primary);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.35s ease;
}

.button:hover {
  transform: translateY(-1px);
  background-color: var(--primary-dark);
}

.button:focus,
.button:active {
  outline: none;
}
